<template>
  <v-container>
    <v-row align="center" justify="center">
      <v-btn color="primary" @click="dialog = !dialog" dark>Add Data</v-btn>
    </v-row>
    <v-row justify="center">
      <v-dialog v-model="dialog" persistent max-width="600px">
        <v-card>
          <v-card-title>
            <span class="headline">Add Data {{image}}</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-row>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field label="Name" v-model="input.name" required></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-file-input
                    accept="image/*"
                    @change="handleimage"
                    label="File input"
                  ></v-file-input>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="dialog = false">Close</v-btn>
            <v-btn color="blue darken-1" text @click="savedata">Save</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
    <v-simple-table>
      <template v-slot:default>
        <thead>
          <tr>
            <th class="text-left">No</th>
            <th class="text-left">Name</th>
            <th class="text-left">Thumb</th>
            <th class="text-left">Image</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in result" :key="item.id">
            <td>{{ index + 1 }}</td>
            <td>{{ item.name }}</td>
            <td>{{ item.image }}</td>
            <td>
              <v-card flat tile class="d-flex">
                <v-img
                  v-if="item.image"
                  :src="`${item.image}`"
                  :lazy-src="`${item.image}`"
                  max-height="50"
                  max-width="50"
                  aspect-ratio="1"
                  class="grey lighten-2"
                >
                  <template v-slot:placeholder>
                    <v-row class="fill-height ma-0" align="center" justify="center">
                      <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
                    </v-row>
                  </template>
                </v-img>
              </v-card>
            </td>
          </tr>
        </tbody>
      </template>
    </v-simple-table>
    <v-row align="center" v-if="result" justify="center">
      <v-btn medium color="primary" @click="loadmore()">Load More</v-btn>
    </v-row>
  </v-container>
</template>

<script>
import qs from "qs";
export default {
  name: "Crud",
  data: () => ({
    result: null,
    dialog: false,
    initlimit: 10,
    plus: 10,
    input: {},
    image: "",
  }),
  methods: {
    getdata() {
      this.isLoading(true);
      const baseURI = `http://localhost/epapp/crud/getall?limit=${this.initlimit}`;
      this.$axios.get(baseURI).then((result) => {
        this.result = result.data["data"];
        this.isLoading(false);
      });
    },
    loadmore() {
      this.initlimit = this.initlimit + this.plus;
      this.getdata();
    },
    setter(val) {
      this.input.image = val;
    },
    handleimage(file) {
      if (file) {
        var reader = new FileReader();
        reader.onload = () => {
          this.setter(reader.result);
        };
        reader.readAsDataURL(file);
      }
    },
    isLoading(param) {
      this.$store.commit("isLoading", param);
    },
    savedata() {
      this.isLoading(true);
      console.log(this.input.image);
      const baseURI = `http://localhost/epapp/crud/uploadbase`;
      this.$axios.post(baseURI, qs.stringify(this.input)).then(
        (result) => {
          console.log(result);
          this.isLoading(false);
        },
        (error) => {
          console.log(error);
          this.isLoading(false);
        }
      );
    },
  },
  mounted() {
    this.getdata();
  },
};
</script>

